import { useState, useEffect, useRef, useContext } from "react";
import toast from "react-hot-toast";
import Layout from 'layouts'
import './Burn.scss'
import { Container } from 'react-bootstrap'

// Import NFT images only

// Import NFT images
import npcNFT from '../assets/GOTCHAOWNERSHIPNFTS/Npc of Gotcha.png'
import squireNFT from '../assets/GOTCHAOWNERSHIPNFTS/Squire of Gotcha.png'
import knightNFT from '../assets/GOTCHAOWNERSHIPNFTS/Knight of Gotcha.png'
import dukeNFT from '../assets/GOTCHAOWNERSHIPNFTS/Duke of Gotcha.png'
import lordNFT from '../assets/GOTCHAOWNERSHIPNFTS/Lord of Gotcha.png'
import kingNFT from '../assets/GOTCHAOWNERSHIPNFTS/King of Gotcha.png'

// Mouse image import removed
import coinImage from '../assets/burnimages/Icon_256_Coin_05.png'

import { useMediaQuery } from '@uidotdev/usehooks'
import { useConnectModal } from "@rainbow-me/rainbowkit";
import { useChainId, useAccount, useReadContract, useWriteContract } from "wagmi";

import Combobox from 'components/Combobox'
import { BURN_CNT_OPTIONS } from 'config/burn'
import 'react-tabs/style/react-tabs.scss'
import {
  TEST_NET,
  GOTCHA_ADDRESS_TEST,
  GOTCHA_ADDRESS_MAIN,
  NEW_GOTCHA_ADDRESS_TEST,
  NEW_GOTCHA_ADDRESS_MAIN,
  GAME_ADDRESS_TEST,
  GAME_ADDRESS_MAIN,
} from "../config";
import GameAbi from "../config/abis/GAME_ABI.json";
import GotchaAbi from "../config/abis/GOTCHA_ABI.json";
import Erc1155Abi from "../config/abis/ERC1155_ABI.json";
import { RefreshContext } from '../context/RefreshContext'

// Add animation keyframes style
const nftAnimationStyle = document.createElement('style');
nftAnimationStyle.innerHTML = `
  @keyframes pulse {
    0% { transform: scale(1); opacity: 1; }
    100% { transform: scale(1.05); opacity: 0.9; }
  }
  
  @keyframes float {
    0% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
    100% { transform: translateY(0px); }
  }
  
  @keyframes glow {
    0% { opacity: 0.5; }
    100% { opacity: 0.8; }
  }
`;
document.head.appendChild(nftAnimationStyle);

const useRefresh = () => {
  const { fast, slow } = useContext(RefreshContext)
  return { fastRefresh: fast, slowRefresh: slow }
}

export default function BettingPage() {
  const isMobile = useMediaQuery("only screen and (max-width: 768px)");
  const { fastRefresh } = useRefresh();

  const { data: txResult, writeContractAsync } = useWriteContract();

  const { openConnectModal } = useConnectModal();

  const chainId = useChainId();
  const { isConnected, address } = useAccount();
  
  // Animation reference
  const animationRef = useRef(null);
  
  // Button animation state
  const [isButtonPressed, setIsButtonPressed] = useState(false);
  const [buttonLayer2Position, setButtonLayer2Position] = useState(0);

  // Custom mouse cursor removed

  /////////Game////////////////////////////////////
  const { data: nftsOnWallet, refetch: refetchGetNFTsInWallet } = useReadContract({
    address: chainId === TEST_NET ? GAME_ADDRESS_TEST : GAME_ADDRESS_MAIN,
    abi: GameAbi,
    functionName: "getTokensInWallet",
    args: [[chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN], address],
    chainId: chainId,
  });

  //////////////////////////////////////////////
  const { data: balance, refetch: refetchBalance } = useReadContract({
    address: chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN,
    abi: GotchaAbi,
    functionName: "balanceOf",
    args: [isConnected ? address : ""],
    chainId: chainId,
  });

  const { data: isApprovedForAll, refetch: refetchIsApprovedForAll } = useReadContract({
    address: chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN,
    abi: GotchaAbi,
    functionName: "isApprovedForAll",
    args: [isConnected ? address : "", chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN],
    chainId: chainId,
  });

  /////////////////////////////////////////////////
  const { data: balanceBox1, refetch: refetchBalanceBox1 } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: Erc1155Abi,
    functionName: "balanceOf",
    args: [isConnected ? address : "", 0],
    chainId: chainId,
  });

  const { data: balanceBox2, refetch: refetchBalanceBox2 } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: Erc1155Abi,
    functionName: "balanceOf",
    args: [isConnected ? address : "", 1],
    chainId: chainId,
  });

  const { data: balanceBox3, refetch: refetchBalanceBox3 } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: Erc1155Abi,
    functionName: "balanceOf",
    args: [isConnected ? address : "", 2],
    chainId: chainId,
  });

  const { data: balanceBox4, refetch: refetchBalanceBox4 } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: Erc1155Abi,
    functionName: "balanceOf",
    args: [isConnected ? address : "", 3],
    chainId: chainId,
  });

  const { data: balanceBox5, refetch: refetchBalanceBox5 } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: Erc1155Abi,
    functionName: "balanceOf",
    args: [isConnected ? address : "", 4],
    chainId: chainId,
  });

  const { data: balanceBox6, refetch: refetchBalanceBox6 } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: Erc1155Abi,
    functionName: "balanceOf",
    args: [isConnected ? address : "", 5],
    chainId: chainId,
  });

  const regInputForExpect = useRef()
  const [selectedAmount, setSelectedAmount] = useState(BURN_CNT_OPTIONS[0]);
  const [isBurning, setIsBurning] = useState(false);
  const [isApproving, setIsApproving] = useState(false);
  const [walletTokenIds, setWalletTokenIds] = useState([]);
  
  // Map NFT images to rank types
  const nftImages = {
    1: npcNFT,        // NPC of Gotcha
    5: squireNFT,     // Squire of Gotcha
    10: knightNFT,    // Knight of Gotcha
    25: dukeNFT,      // Duke of Gotcha
    50: lordNFT,      // Lord of Gotcha
    100: kingNFT      // King of Gotcha
  };

  useEffect(() => {
    refetchGetNFTsInWallet();

    refetchBalance();
    refetchIsApprovedForAll();

    refetchBalanceBox1();
    refetchBalanceBox2();
    refetchBalanceBox3();
    refetchBalanceBox4();
    refetchBalanceBox5();
    refetchBalanceBox6();
  }, [fastRefresh])

  useEffect(() => {
    if (nftsOnWallet?.length > 0) {
      const tokenids = nftsOnWallet.map((item) => {
        return Number(item.token_id);
      });

      setWalletTokenIds(tokenids);
    } else {
      setWalletTokenIds([]);
    }
  }, [nftsOnWallet]);

  const getTextBurn = () => {
    if (isApproving)
      return "Approving...";

    if (isBurning)
      return "SUMMONING MAGIC...";

    if (!isApprovedForAll)
      return "Approve";

    const firstTokenIds = walletTokenIds.slice(0, selectedAmount.value);
    if (firstTokenIds.length !== selectedAmount.value)
      return "Insufficient NFT Balance";

    return "Burn";
  };

  // No animation functions needed for simple image popup

  // Custom mouse cursor code removed
  
  // Stars effect removed
  
  // No idle animation needed

  // No NFT animation function needed for simple image popup

  // Simple animation cleanup
  useEffect(() => {
    return () => {
      if (animationRef.current) {
        clearTimeout(animationRef.current);
      }
    };
  }, [isBurning]);

  const handleBurn = async () => {
    if (!isConnected) {
      openConnectModal();
      return;
    }

    if (!isApprovedForAll) {
      setIsApproving(true);
      await writeContractAsync({
        address: chainId === TEST_NET ? GOTCHA_ADDRESS_TEST : GOTCHA_ADDRESS_MAIN,
        abi: GotchaAbi,
        functionName: "setApprovalForAll",
        args: [chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN, true]
      })
        .then(() => {
          setIsApproving(false);
        })
        .catch((err) => {
          setIsApproving(false);
        });
      return;
    }

    const firstTokenIds = walletTokenIds.slice(0, selectedAmount.value);
    if (firstTokenIds.length !== selectedAmount.value) return;

    setIsBurning(true);
    await writeContractAsync({
      address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
      abi: Erc1155Abi,
      functionName: "burn",
      args: [firstTokenIds, "0x"]
    })
      .then(() => {
        setTimeout(() => {
          setIsBurning(false);
        }, 3000); // Keep animation running for a bit after burn completes
      })
      .catch((err) => {
        setIsBurning(false);
      });
  }

  return (
    <Layout>
      {/* Futuristic background glow overlay */}
      <div className="glow-overlay"></div>
      
      {/* Floating coins background */}
      <div className="floating-coins-container">
        {Array.from({ length: isMobile ? 5 : 11 }).map((_, index) => (
          <div key={index} className={`floating-coin coin-${index + 1}`}>
            <img src={coinImage} alt="Coin" />
          </div>
        ))}
      </div>
      
      {/* Custom mouse cursor removed */}
      
      {
        isConnected ?
          <>
            <Container fluid>
            
              
              {/* Chest animation container */}
              <div className="chest-animation-container">
                {/* Control panel at the top */}
                <div className="control-panel">
                  <div className="panel-background">
                    <div className="panel-glow"></div>
                    
                    {/* Balance Display */}
                    <div className="balance-section">
                      <div className="balance-orb">
                        <div className="orb-inner">
                          <span className="orb-value">{parseInt(balance ? balance : 0)}</span>
                        </div>
                        <div className="orb-ring"></div>
                      </div>
                      <div className="balance-info">
                        <h3 className="balance-label">GOTCHA NFTs</h3>
                        <p className="balance-subtitle">Available in Wallet</p>
                      </div>
                    </div>
                    
                    {/* Amount Selection */}
                    <div className="amount-section">
                      <h3 className="amount-label">BURN AMOUNT</h3>
                      <div className="amount-selector">
                        <Combobox 
                          ref={regInputForExpect} 
                          options={BURN_CNT_OPTIONS} 
                          value={selectedAmount} 
                          onChange={(selectedOption) => setSelectedAmount(selectedOption)} 
                        />
                      </div>
                      <div className="title-display">
                        {selectedAmount?.value === 1 && <div className="title-badge npc">NPC of Gotcha</div>}
                        {selectedAmount?.value === 5 && <div className="title-badge squire">Squire of Gotcha</div>}
                        {selectedAmount?.value === 10 && <div className="title-badge knight">Knight of Gotcha</div>}
                        {selectedAmount?.value === 25 && <div className="title-badge duke">Duke of Gotcha</div>}
                        {selectedAmount?.value === 50 && <div className="title-badge lord">Lord of Gotcha</div>}
                        {selectedAmount?.value === 100 && <div className="title-badge king">King of Gotcha</div>}
                        {!selectedAmount?.value && <div className="title-badge empty">Select amount to view title</div>}
                      </div>
                    </div>
                    
                    {/* Action Button */}
                    <div 
                      className={`action-button ${isButtonPressed ? 'pressed' : ''}`}
                      onMouseDown={() => setIsButtonPressed(true)}
                      onMouseUp={() => setIsButtonPressed(false)}
                      onMouseLeave={() => setIsButtonPressed(false)}
                      onClick={handleBurn}
                    >
                      <div className="button-bg"></div>
                      <div className="button-particles"></div>
                      <span className="button-text">
                        {getTextBurn()}
                      </span>
                    </div>
                  </div>
                </div>
                
                <div className="nft-simple-display" style={{ textAlign: 'center', marginTop: '20px' }}>
                  {/* Just the NFT image with animation */}
                  {!isBurning ? (
                    <img 
                      src={nftImages[selectedAmount?.value] || nftImages[1]} 
                      alt="NFT Preview" 
                      style={{
                        width: '250px',
                        height: '250px',
                        objectFit: 'contain',
                        filter: 'drop-shadow(0 0 8px rgba(0, 150, 255, 0.5))',
                        transition: 'all 0.3s ease'
                      }}
                    />
                  ) : (
                    <img 
                      src={nftImages[selectedAmount?.value] || nftImages[1]} 
                      alt="NFT Being Summoned" 
                      style={{
                        width: '250px',
                        height: '250px',
                        objectFit: 'contain',
                        filter: 'brightness(1.2) drop-shadow(0 0 15px rgba(0, 200, 255, 0.8))',
                        animation: 'float 3s infinite ease-in-out, pulse 1.5s infinite alternate'
                      }}
                    />
                  )}
                  
             
                </div>

              </div>
              
              {/* NFT Boxes display */}
             
                
            </Container>
          </>
          :
          <>
            <Container fluid>
              <div className="disconnected-container" style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                minHeight: isMobile ? 'auto' : '70vh',
                padding: isMobile ? '20px' : '40px',
                maxWidth: isMobile ? '100%' : '1200px',
                margin: '0 auto'
              }}>
                <div className="nft-simple-display" style={{ 
                  textAlign: 'center', 
                  marginTop: '20px',
                  background: 'rgba(0, 20, 40, 0.3)',
                  borderRadius: '16px',
                  padding: isMobile ? '20px' : '40px',
                  boxShadow: '0 0 30px rgba(0, 100, 200, 0.2)',
                  backdropFilter: 'blur(10px)',
                  border: '1px solid rgba(0, 100, 200, 0.2)',
                  width: isMobile ? '100%' : '600px'
                }}>
                  <img 
                    src={nftImages[25]} 
                    alt="NFT Preview" 
                    style={{
                      width: isMobile ? '250px' : '300px',
                      height: isMobile ? '250px' : '300px',
                      objectFit: 'contain',
                      filter: 'drop-shadow(0 0 15px rgba(0, 150, 255, 0.6))',
                      marginBottom: '30px',
                      transition: 'all 0.3s ease',
                      animation: 'pulse 3s infinite alternate'
                    }}
                  />
                  <div className="title-display" style={{ marginBottom: '20px' }}>
                    <div 
                      className="title-badge connect" 
                      style={{ 
                        cursor: 'pointer',
                        padding: isMobile ? '10px 20px' : '15px 30px',
                        fontSize: isMobile ? '16px' : '18px',
                        transition: 'all 0.3s ease',
                        animation: 'pulse 2s infinite alternate'
                      }}
                      onClick={openConnectModal}
                    >
                      <span style={{ fontFamily: 'THEBOLDFONT', letterSpacing: '1px', fontSize: isMobile ? '18px' : '20px' }}>CONNECT TO BURN NFTs</span>
                    </div>
                  </div>
                </div>
             
              </div>
            </Container>
          </>
      }
    </Layout>
  )
}
